<template lang="pug">
  .container.py-3
    h5.mt-1.mb-3.my-sm-3.d-flex.align-items-center
      b-img(
        src="https://pp.userapi.com/c845121/v845121722/f4230/7A8-LrWwZlo.jpg?ava=1",
        alt="",
        rounded="circle",
        class="img-thumbnail mr-3")
      span
        | Старообрядцы в Крыму
        small.form-text.text-muted (генератор изображения для VK)
    b-card(no-body)
      b-tabs(card)
        b-tab(title="Текст", title-item-class="w-50" active)
          vue-editor(v-model="content", :editorToolbar="customToolbar")
          .pt-3.d-flex.d-sm-block.text-right
            b-button(@click="clear", class="flex-fill", :disabled="content === ''") Очистить
        b-tab(title="Результат", title-item-class="w-50")
          .result.ql-editor
            .p-2(ref="result", v-html="content")
          .row
            .col-12.col-sm-7.col-md-8.col-lg-9
              b-form-group(label="Имя изображения:", label-for="filename")
                b-form-input#filename(type='text', v-model='filename')
            .col-12.col-sm-5.col-md-4.col-lg-3
              b-form-group(label="&nbsp;", label-class="btn-label")
                b-button(variant="primary", @click="print", class="flex-fill", block, :disabled="content === ''") Сохранить картинку
</template>

<script>
import { VueEditor } from 'vue2-editor'

export default {
  name: 'Home',
  components: {
    VueEditor
  },
  data () {
    return {
      content: '',
      filename: 'Старообрядцы_в_Крыму_img_vk',
      customToolbar: [
        [{ 'header': [1, 2, 3, 4, 5, 6, false] }],
        ['bold', 'italic'],
        [{ 'align': [] }],
        [{ 'color': [] }],
        ['clean']
      ]
    }
  },
  methods: {
    print () {
      const el = this.$refs.result
      const options = {
        type: 'dataURL',
        scale: 3
      }
      this.$html2canvas(el, options)
        .then(output => {
          const link = document.createElement('a')
          link.download = `${this.filename}.png`
          link.href = output
          link.click()
          link.remove()
        })
        .catch(error => console.error(error)) // eslint-disable-line
    },
    clear () {
      this.content = ''
    }
  }
}
</script>

<style lang="stylus">
  .ql-editor
    font-family triodion-ieUcs
    min-height 250px !important

  .result
    max-width 540px
    margin 0 auto

  @media (max-width 575px)
    .ql-toolbar.ql-snow .ql-formats
      margin-right 5px !important

    .btn-label
      display none
</style>
